<template>
    <div class="popup-info">
        <div>
        </div>
        
        <div class="text mt32">
            <div class="text-size-xxl text-color-green text-bolder">Ваш вопрос принят!</div>
            
            <p class="mt24">Мы прочитаем его в любом случае и опубликуем, если он окажется интересным...</p>
        </div>
        
        <div class="mt32">
            <button class="btn btn--white-green" @click="newQuestModalClose()">Степенно жду!</button>
        </div>
    </div>
</template>

<script>
    import Popup from '@/components/modal/Popup.vue'
    import {mapState} from 'vuex'

    export default {
        name: "newQuest",
        methods: {
            newQuestModalClose: function () {
                this.$store.commit('popups/close', {name: this.popups.newQuest.name});
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

