<template>
    <div class="content faq-page">
        <div class="main-title">
              <div class="page-ttl" style='justify-content: center'>Вопросы и ответы</div>
        </div>


        <section class="container">
            <div class="faq-list">
                <div class="faq-item" v-for="item in items" :key="item.id">
                    <div class="h2 text-color">{{ item.name }}</div>
                    <p class="text-color" v-html="replaceUrl(item.description)"></p>
                </div>
            </div>

            <div class="button-block mt32">
                <button class="btn" @click="openAskQuestion()">Задать вопрос</button>
            </div>
        </section>
        <!-- end container -->

        <popup-info-block :show="popups.newQuest.show"
               :is-set-blank="false"
               :add-class="'.newQuest'"
               :title="''"
               :modal-type="''"
               :text="''"
               :btntext="'Степенно жду!'"
               :name="popups.newQuest.name">
               <div class="modal-txt">
                   <svg class="icon">
                       <use xlink:href="@/assets/img/mob-sprite.svg#emoji-new-quest"></use>
                   </svg>
                  <div class="modal-ttl">Ваш вопрос принят!</div>
                 <p>Мы прочитаем его в любом случае<br>и опубликуем, если он окажется<br>интересным...</p>
               </div>
        </popup-info-block>
        <popup :show="popups.askQuestion.show"
               :is-set-blank="isBanned"
               :title="'Задать вопрос Алексею Земскову'"
               :name="popups.askQuestion.name">
               <div>
                   <textarea-field class="full mt24" :add-style="{height: 200 + 'px'}" @focus="quest_error = false" :class="{error: quest_error}" v-model="questText" v-bind:style="{ resize: 'none' }" placeholder="Например, «А бесплатные купоны – это вообще законно?!»"></textarea-field>
               </div>


               <div class="row-b mt32 button-block">
                   <button class="btn" @click="sendQuest()">Отправить</button>
               </div>
        </popup>
    </div>
</template>



<script>
    import PopupInfoBlock from '@/components/modal/PopupInfoBlock.vue'
    import NewQuest from '@/components/modal/NewQuest.vue'
    import Popup from '@/components/modal/Popup.vue'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import {mapState} from 'vuex'

    export default {
        metaInfo() {
            return {
                title: 'Вопросы и ответы',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Faq",
        components: {
            PopupInfoBlock,
            NewQuest,
            TextareaField,
            Popup
        },
        data: function() {
            return {
                questText: '',
                quest_error: false,
                send_quest_success: true
            }
        },
        created() {
            this.$store.dispatch('faq/getList');
        },
        mounted: function () {
            ym(39511350, 'hit', this.$route.path, {title: 'Вопросы и ответы | ZemSkidki.ru'});
        },
        methods: {
            sendQuest: function() {
                let  self = this;
                self.quest_error = false;
                if (self.questText === '') {
                    setTimeout(function () {
                        self.quest_error = true;
                    }, 300);
                } else {
                    self.quest_error = false;
                    this.$store.dispatch('faq/sendQuest', {quest: self.questText}).then(()=>{
                        self.questText = '';
                    });
                    self.newQuestModalOpen();
                }
            },
            newQuestModalOpen: function () {
                this.send_quest_success = false;
                this.$store.commit('popups/open', {name: this.popups.newQuest.name});
            },
            openAskQuestion: function () {
                this.$store.commit('popups/open', {name: this.popups.askQuestion.name});
            },
            replaceUrl(text){
                return text.replace('zemskidki.ru','m.zemskidki.ru')
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
            items() {
                this.send_quest_success = true;
                return this.$store.getters['faq/getList']();
            },
        }
    }
</script>
